import React, { useEffect, useState, useRef } from "react";
import { motion } from "framer-motion";

import Background from './components/background';
import Intro from './pages/intro';
import Header from "./components/header";
import Page1 from './pages/page1';
import Page2 from './pages/page2';
import Page3 from './pages/page3';
import Page4 from './pages/page4';
import Page5 from './pages/page5';
import BackgroundChange from "./components/backgroundChage";

import './App.scss';
import './styles/style.scss';

function App() {
  const [isShow, setIsShow] = useState(false);
  const [isSmall, setIsSmall] = useState(false);

  const page1Ref = useRef(null);
  const page2Ref = useRef(null);
  const page3Ref = useRef(null);
  const page4Ref = useRef(null);
  const page5Ref = useRef(null);

  useEffect(() => {
    const timeout = setTimeout(() => {
      setIsShow(true);
    }, 3500);
    return () => clearTimeout(timeout);
  }, []);

  useEffect(() => {
    const handleResize = () => {
      setIsSmall(window.innerWidth > 320);
    };

    window.addEventListener('resize', handleResize);
    handleResize();
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (
    <body>
      <Background />
      <Intro />
      <motion.div 
        className="content"
        initial={{opacity: 0, y: 25 }} 
        animate={{ opacity: isShow ? 1 : 0, y: isShow ? 0 : 50 }} 
        transition={{ delay: 0.3, duration: 0.4, ease: "easeInOut" }}>
        {isSmall && (
          <>
            {isShow && (
              <div>
                <Header
                  refs={{
                    page1: page1Ref, page2: page2Ref, page3: page3Ref, page4: page4Ref, page5: page5Ref
                  }}
                />
                <div ref={page1Ref} style={{ paddingTop: "" }}>
                  <Page1 refs={{
                    page3: page3Ref,
                  }}/>
                </div>
                <div ref={page2Ref}>
                  <Page2 />
                </div>
                <div ref={page3Ref}>
                  <Page3 />
                </div>
                <div ref={page4Ref}>
                  <Page4 />
                </div>
                <div ref={page5Ref}>
                  <Page5 />
                </div>
              </div>
            )}
          </>
        )}
      </motion.div>
      {!isSmall && (
        <div className="small-canvas">
          <h2>Its Getting A bit Tight</h2>
          <br />
          <br />
          <h2>Seems like your screen is too small</h2>
        </div>
      )}
    </body>
  );
}

export default App;
