import React, {useEffect, useState, useRef} from "react";
import ARTEx from "../components/ARTEx 3D";

function App({ refs }) {
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };
    window.addEventListener("scroll", handleScroll);

    // Cleanup function
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const scrollToRef = (ref, isPage1 = false) => {
    if (ref && ref.current) {
      const topPos = ref.current.getBoundingClientRect().top + window.scrollY;
      window.scrollTo({ top: topPos -65, behavior: "smooth" });
    }
  };


  
  const page1Ref = useRef(null);

  return (
    <div className="page1" ref={page1Ref}>  
        <h2 className="homePage-title">Most recent Project</h2> 
        <h6 className="homePage-h6">All-terrain Robotic Transportation and Exploration System (University Project)</h6>
        <h3 className="homePage-h3">ARTEx</h3>
        <div className="homePage-display" ><ARTEx/></div>
        <div style={{position:"absolute", display:"flex", bottom:"50px", right:"0"}}>
          <button className="homePage-button" onClick={() => scrollToRef(refs.page3)}><h3 className="homePage-text">View More Projects</h3>
          </button>
        </div>
    </div>   
  );
}

export default App;
