import React, {useRef, useState, useEffect} from "react";

function App() {
  const [isVisible, setIsVisible] = useState(false);
  useEffect(() => {
    const handleResize = () => {
      // Update isVisible based on screen width
      setIsVisible(window.innerWidth > 675); // Set isVisible to true if screen width is greater than 768px
    };

    // Add event listener to handle window resize
    window.addEventListener('resize', handleResize);

    // Initial call to handleResize
    handleResize();

    // Cleanup function to remove event listener
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const showArsenal = () => {
    setIsVisible(!isVisible);
  };

  const page2Ref = useRef(null);

  return (
    <div className="order" ref={page2Ref} style={{height:"650px", borderBottom:"3px solid var(--font-color)"}}>
      <div className="page-display">
          <h1 className="titles">Skills</h1>
          {isVisible && ( <div className="homepage-movement"><h3>my proficiency</h3>
          <div className="icon-container-container">
            <button className="icon-container"><img className="icons" src={require("../images/Icons/node-js.png")}/><div className="home-noPro"></div><h5>Node.js</h5></button>
            <button className="icon-container"><img className="icons" src={require("../images/Icons/html5.png")}/><div className="home-htPro"></div><h5>HTML</h5></button>
            <button className="icon-container"><img className="icons" src={require("../images/Icons/css3.png")}/><div className="home-csPro"></div><h5>CSS</h5></button>
            <button className="icon-container"><img className="icons" src={require("../images/Icons/sass.png")}/><div className="home-saPro"></div><h5>SASS</h5></button>
            <button className="icon-container"><img className="icons" src={require("../images/Icons/js.png")}/><div className="home-jsPro"></div><h5>JavaScript</h5></button>
            <button className="icon-container"><img className="icons" src={require("../images/Icons/java.png")}/><div className="home-jaPro"></div><h5>Java</h5></button>
            <button className="icon-container"><img className="icons" src={require("../images/Icons/python.png")}/><div className="home-pyPro"></div><h5>Python</h5></button>
            <button className="icon-container"><img className="icons" src={require("../images/Icons/c.png")}/><div className="home-cPro"></div><h5>C Language</h5></button>
            <button className="icon-container"><img className="icons" src={require("../images/Icons/mysql.png")}/><div className="home-sqPro"></div><h5>SQL</h5></button>
            <button className="icon-container"><img className="icons" src={require("../images/Icons/unity.png")}/><div className="home-unPro"></div><h5>Unity</h5></button>
          </div>
          <div className="icon-button-switch-container">
          <button className="icon-button-switch" onClick={showArsenal}>View Contact Links</button>
          </div></div>)}

              {!isVisible && (<div className="homepage-movement"><h3>My Links</h3>
                <div className="icon-container-container">
                  <button className="icon-container"><img className="icons" src={require("../images/Icons/email.png")}/><div className="home-conPro"></div><h5>Email</h5></button>                    
                  <button className="icon-container"><img className="icons" src={require("../images/Icons/github.png")}/><div className="home-conPro"></div><h5>Github</h5></button>
                  <button className="icon-container"><img className="icons" src={require("../images/Icons/linkedin.png")}/><div className="home-conPro"></div><h5>linkedin</h5></button>                    
                </div>
                <div className="icon-button-switch-container">
                  <button className="icon-button-switch" onClick={showArsenal}>View Arsenal</button>
                </div></div>)}
           
        </div>
        <div>
          
        </div>
      </div>
  );
}

export default App;
